import { NotificationTypes, ADD_NOTIFICATION, REMOVE_NOTIFICATION } from './types';
export function addNotification(notification) {
    return {
        type: ADD_NOTIFICATION,
        notification: notification
    };
}
export function removeNotification(notificationId) {
    return {
        type: REMOVE_NOTIFICATION,
        notificationId: notificationId
    };
}
// gestion basique d'une file de notifications!
var currentNotificationId = 0;
var timeoutId;
export function queueNotification(dispatch, message, type, duration) {
    if (duration === void 0) { duration = 2000; }
    var id = currentNotificationId++;
    var notification = {
        id: id,
        message: message,
        timestamp: Date.now(),
        type: type,
    };
    dispatch(addNotification(notification));
    if (type === NotificationTypes.temporary) {
        notification.duration = duration;
        timeoutId = setTimeout(function () {
            dispatch(removeNotification(id));
        }, notification.duration);
    }
    return notification;
}
export function updateNotification(id, message, type) {
}
