import React from 'react';
import { useSelector } from 'react-redux';
import './notifications.css';
var Notifications = function () {
    var selectNotifications = function (state) { return state.notifications; };
    var notifications = useSelector(selectNotifications);
    return React.createElement("div", { className: "Notifications" }, notifications.map(function (v, key) {
        return React.createElement("div", { className: "Notification", key: v.id },
            React.createElement("div", { className: "Message" }, v.message),
            React.createElement("div", { className: "Action" }));
    }));
};
export default Notifications;
